import React from 'react';
import layouts from './layout.json';
import { hasAccess, LayoutEngine, TopBar, useForceRender } from '@ab/framework';
import { themeProvider } from '@ab/common';
import { ThemeProvider } from 'material-ui';

/************************ Microfrontend import and named export **********************************/

import { Widget as Widget1 } from '@ab/w-balance-card';
import { Widget as Widget2 } from '@ab/w-card-spend';

const getById = (mfId) => {
  switch (mfId) {
    case '@ab/w-balance-card':
      return Widget1;
    case '@ab/w-card-spend':
      return Widget2;
  }
};

/************************ Microfrontend import and named export **********************************/

const useWidgetById =
  ({ permissions, profile }) =>
  (mfId, reactProps) => {
    let Widget = getById(mfId) || <></>;
    return hasAccess(permissions, mfId) ? (
      <ThemeProvider theme={themeProvider}>
        <Widget {...reactProps} profile={profile} />
      </ThemeProvider>
    ) : (
      <></>
    );
  };

export default function Root(props) {
  
  const [vars] = useForceRender();

  return <>
    <TopBar profile={props.profile} />
    <LayoutEngine getWdigetById={useWidgetById(props)} layouts={layouts} country={vars.country || 'default'} />
  </>
}
